<template>
  <div class="bj">
    <div class="column">
      {{ data }}
      <a href="https://blog.csdn.net/TroyeSivanlp/article/details/123271225">fetch说明</a>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import dayjs from "dayjs";

// import { dateTransfor } from "../utils";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  props: {
    // arr: {
    //   type: Array,
    //   default:()=>[]
    // },
  },
  components: {},
  data() {
    return {
      currentDate: null,
      data: ""
    };
  },
  created() {
    let vm = this;
    // fetch("./test.json")
    // .then((response) => {
    //   console.log("res", response.json());
    // })
    // .catch((error) => {
    //   console.log("error", error);
    // });

    fetch(
      "https://api.apiopen.top/api/sentences"
      // , {
      //   method: "GET",
      //   mode: "cors", // 允许发送跨域请求
      // }
    )
      .then(function(response) {
        //打印返回的json数据
        response.json().then(function(data) {
          console.log(typeof data);
          vm.data = data.result;
        });
      })
      .catch(function(e) {
        console.log("error: " + e.toString());
      });
  },
  mounted() {
    // async function request() {
    //   const response = await fetch("./test.json");
    //   console.log("mounted", response.json()); //
    // }
    // request();
    fetch('https://api.github.com/users/ruanyf')
  .then(response => response.json())
  .then(json => console.log(json))
  .catch(err => console.log('Request Failed', err)); 

  },
  methods: {
    ...mapMutations({
      changeDataTest: "changeDataTest"
    }),
//fetch()使用 Promise，不使用回调函数，因此大大简化了写法，写起来更简洁。
// fetch()采用模块化设计，API 分散在多个对象上（Response 对象、Request 对象、Headers 对象），更合理一些；相比之下，XMLHttpRequest 的 API 设计并不是很好，输入、输出、状态都在同一个接口管理，容易写出非常混乱的代码。
// fetch()通过数据流（Stream 对象）处理数据，可以分块读取，有利于提高网站性能表现，减少内存占用，对于请求大文件或者网速慢的场景相当有用。XMLHTTPRequest 对象不支持数据流，所有的数据必须放在缓存里，不支持分块读取，必须等待全部拿到后，再一次性吐出来。
// 在用法上，fetch()接受一个 URL 字符串作为参数，默认向该网址发出 GET 请求，返回一个 Promise 对象

//原文链接：https://blog.csdn.net/TroyeSivanlp/article/details/123271225
  },
  watch: {
    //'list.xx':{} 监听一个属性的写法
    // msg: {
    //   handler(curVal, oldVal) {
    //     //改变其他变量的操作
    //   },
    //   immediately: true,
    //   deep: true,
    // },
  },
  computed: {
    // num() {
    //   //用其他变量计算 return出值
    //   return "num";
    // },

    //防止数据更新不及时 放computed获取
    ...mapState({
      dataTest: state => state.dataTest
    })
  },
  filters: {
    //这里定义的会覆盖全局
    //千分位
    // NumFormat: function (value) {
    //   if (value != null || "") {
    //     return value.toLocaleString("en-US");
    //   }
    // }
  }
};
</script>

<style lang="less" scoped>
// @import "../main.less";

input::-webkit-input-placeholder {
  // 设置输入框文字颜色
}

div::-webkit-scrollbar,
span::-webkit-scrollbar {
  // 设置滚动条样式
  display: none;
}

/deep/.van-picker {
  width: 100vw;
}

span,
img {
  display: inline-block;
}

div,
span {
  -webkit-overflow-scrolling: touch;
}

.bj {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-size: 100% 100%;
}
</style>



