<template>
  <div class="bj">
    <ball />
    <div class="column">
      <span class="spanBox" :key="key" v-for="(item, key) in list">
        <img src="../assets/x.png" />
        <!-- <span class="spanInput"> -->
        <input placeholder="请输入数字" v-model="item.key" />
        <!-- </span> -->
        <span class="spanClick">
          <span class="confirm" @click.native="confirmF">确认</span>
          <span class="modify" @click.native="modifyF">修改</span>
        </span>
      </span>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import dayjs from "dayjs";
import ball from './ball.vue';
// import { dateTransfor } from "../utils";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  props: {
    // arr: {
    //   type: Array,
    //   default:()=>[]
    // },
  },
  components: {
    ball
  },
  data() {
    return {
      currentDate: null,
      list: [
        { key: "" },
        { key: "" },
        { key: "" },
        { key: "" },
        { key: "" },
        { key: "" },
        { key: "" },
        { key: "" },
        { key: "" }
      ]
    };
  },
  created() {},
  mounted() {},
  methods: {
    ...mapMutations({
      changeDataTest: "changeDataTest"
    }),
    confirmF() {},
    modifyF() {}
  },
  watch: {
    //'list.xx':{} 监听一个属性的写法
    // msg: {
    //   handler(curVal, oldVal) {
    //     //改变其他变量的操作
    //   },
    //   immediately: true,
    //   deep: true,
    // },
  },
  computed: {
    // num() {
    //   //用其他变量计算 return出值
    //   return "num";
    // },

    //防止数据更新不及时 放computed获取
    ...mapState({
      dataTest: state => state.dataTest
    })
  },
  filters: {
    //这里定义的会覆盖全局
    //千分位
    // NumFormat: function (value) {
    //   if (value != null || "") {
    //     return value.toLocaleString("en-US");
    //   }
    // }
  }
};
</script>

<style lang="less" scoped>
// @import "../main.less";

input::-webkit-input-placeholder {
  // 设置输入框文字颜色
  color: #000000;
}

div::-webkit-scrollbar,
span::-webkit-scrollbar {
  // 设置滚动条样式
  display: none;
}

/deep/.van-picker {
  width: 100vw;
}

span,
img {
  display: inline-block;
  box-sizing: border-box;
}

div,
span {
  -webkit-overflow-scrolling: touch;
}

.bj {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-size: 100% 100%;

  .column {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    width: 690px;
    height: 100vh;
    overflow-y: auto;
    margin-left: 30px;
    margin-top: 30px;
    margin-bottom: 30px;
    padding-bottom: 30px;
    .spanBox {
      // margin-right:30px;
      margin-bottom: 60px;
      width: 27%;
      height: 500px;
      font-size: 26px;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: space-between;
      img {
        width: 100%;
        height: 350px;
      }
      // .spanInput {
      //   width: 100%;
      //   // text-align: center;
      //   display: flex;
      //  padding-left: 1px;
      input {
        display: inline-block;
        width: 80%;
        height: 30px;
        border: 1px solid #000000;
      }
      // }
      .spanClick {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .confirm {
          width: 45%;
          height: 40px;
          text-align: center;
          line-height: 40px;
          background: rgb(0, 179, 255);
        }
        .modify {
          width: 45%;
          text-align: center;
          line-height: 40px;
          height: 40px;
          background: #0080ff;
          color: white;
        }
      }
    }
  }
}
</style>



