import Vue from "vue";
import Router from "vue-router";
// import HelloWorld from '@/components/HelloWorld'
import Home from "@/views/Home";
import demoBasic from "@/views/demoBasic";
import dayjs from "@/views/dayjs";
import proxy from "@/views/proxy";
import scroll from "@/views/scroll";
import debounceAndThrottle from "@/views/debounceAndThrottle";
import filter from "@/views/filter";
import persistedstate from "@/views/persistedstate";
import echart from "@/views/echart";
import mock from "@/views/mock";
import flex from "@/views/flex";
import picPress from "@/views/picPress";
import grid from "@/views/grid";
import dataType from "@/views/dataType";
import ts from "@/views/ts";
import underscore from "@/views/underscore";
import promiseItem from "@/views/promise";
import picToText from "@/views/picToText";
import test1 from "@/views/1test";
import fetch from "@/views/fetch";
import testmobile from "@/views/testmobile";
import testpc from "@/views/testpc";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/testMobile",
      name: "testMobile",
      component: testmobile
    },
    {
      path: "/testPc",
      name: "testPc",
      component: testpc
    },
    {
      path: "/fetch",
      name: "fetch",
      component: fetch
    },
    {
      path: "/test1",
      name: "test1",
      component: test1
    },{
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/dayjs",
      name: "dayjs",
      component: dayjs
    },
    {
      path: "/demoBasic",
      name: "demoBasic",
      component: demoBasic
    },
    {
      path: "/proxy",
      name: "proxy",
      component: proxy
    },
    {
      path: "/scroll",
      name: "scroll",
      component: scroll
    },
    {
      path: "/debounceAndThrottle",
      name: "debounceAndThrottle",
      component: debounceAndThrottle
    },
    {
      path: "/filter",
      name: "filter",
      component: filter
    },
    {
      path: "/persistedstate",
      name: "persistedstate",
      component: persistedstate
    },
    {
      path: "/echart",
      name: "echart",
      component: echart
    },
    {
      path: "/mock",
      name: "mock",
      component: mock
    },
    {
      path: "/flex",
      name: "flex",
      component: flex
    },
    {
      path: "/picPress",
      name: "picPress",
      component: picPress
    },
    {
      path: "/grid",
      name: "grid",
      component: grid
    },
    {
      path: "/dataType",
      name: "dataType",
      component: dataType
    },
    {
      path: "/ts",
      name: "ts",
      component: ts
    },
    {
      path: "/underscore",
      name: "underscore",
      component: underscore
    },
    {
      path:'/promise',
      name:"promise",
      component:promiseItem
    },
    {
      path:'/picToText',
      name:"picToText",
      component:picToText
    },
    

  ]
});



