<template>
  <div class="bj">
    <div class="column">
      <div v-for="item in list1" :key="item.key">
        {{ item.name }}{{ item.key }}
      </div>
      <div v-for="item in list2" :key="item.key">{{ item.name }}</div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import dayjs from "dayjs";

// import { dateTransfor } from "../utils";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  props: {
    // arr: {
    //   type: Array,
    //   default:()=>[]
    // },
  },
  components: {},
  data() {
    return {
      currentDate: null,
      list1: [
        { name: "a", key: Symbol() },
        { name: "a", key: Symbol() },
        { name: "a", key: Symbol() }
      ],
      list2: [
        { name: "b", key: Symbol() },
        { name: "b", key: Symbol() },
        { name: "b", key: Symbol() }
      ]
    };
  },
  created() {
    console.log();
    console.log(Symbol());
    // this.testMapAndForeach()
    this.testMapAndForeach1()
    // this.iteratorT();
    // this.setIterator();
  },
  mounted() {},
  methods: {
    iteratorT() {
      let values = [1, 2, 3];
      // let values = {'a':'a'};
      console.log("values", values);
      let iterator = values[Symbol.iterator]();
      console.log("values[Symbol.iterator]", values[Symbol.iterator]);
      // console.log(iterator.next()); // {value: 1, done: false}
      // console.log(iterator.next()); // {value: 2, done: false}
      // console.log(iterator.next()); // {value: 3, done: false}
      // console.log(iterator.next()); // {value: undefined, done: true}
      // console.log(iterator.next()); // {value: undefined, done: true}
    },
    setIterator() {
      function createIterator(items) {
        let i = 0;
        console.log('i first',i)
        return {
          next: function() {
            console.log('i last',i)
            return i < items.length
              ? { value: items[i++], done: false }
              : { value: undefined, done: true };
          }
        };
      }
      //这是闭包函数
      // console.log(createIterator([1, 2, 3]).next())
      // // console.log(createIterator([1, 2, 3]).next())


      let iterator = createIterator([1, 2, 3]);
      console.log(iterator.next()); // {value: 1, done: false}
      console.log(iterator.next()); // {value: 2, done: false}
      console.log(iterator.next()); // {value: 3, done: false}
      console.log(iterator.next()); // {value: undefined, done: true}
      console.log(iterator.next()); // {value: undefined, done: true}
    },
    testMapAndForeach() {
      let x = [1, 2, 3];
      x.map(item => item + 1);
      console.log("x", x);
      console.log(
        "x",
        x.map(item => item + 1)
      );

      let xx = [1, 2, 3];
      console.log("xx", xx);
      console.log(
        "xx",
        xx.forEach(item => item + 1)
      );
      console.log(
        "xx",
        xx.filter(item => item == 1)
      );
      console.log("xx", xx);

      let xxx = { a: 1, b: 2 };
      console.log(
        "xxx",
        xxx.forEach(item => (item = 1))
      );
    },
    testMapAndForeach1() {
      let x = [1, 2, 3];
     
  
      console.log(
        "x",
        x.map(item => {if(item==1){return 2}})
      )
      console.log("x", x);

      let xx = [1, 2, 3];
    
      console.log(
        "xx",
        xx.forEach(item => {if(item==1){return 2}})
      );
      console.log("xx", xx);
    },
    ...mapMutations({
      changeDataTest: "changeDataTest"
    })
  },
  watch: {
    //'list.xx':{} 监听一个属性的写法
    // msg: {
    //   handler(curVal, oldVal) {
    //     //改变其他变量的操作
    //   },
    //   immediately: true,
    //   deep: true,
    // },
  },
  computed: {
    // num() {
    //   //用其他变量计算 return出值
    //   return "num";
    // },

    //防止数据更新不及时 放computed获取
    ...mapState({
      dataTest: state => state.dataTest
    })
  },
  filters: {
    //这里定义的会覆盖全局
    //千分位
    // NumFormat: function (value) {
    //   if (value != null || "") {
    //     return value.toLocaleString("en-US");
    //   }
    // }
  }
};
</script>

<style lang="less" scoped>
// @import "../main.less";

input::-webkit-input-placeholder {
  // 设置输入框文字颜色
}

div::-webkit-scrollbar,
span::-webkit-scrollbar {
  // 设置滚动条样式
  display: none;
}

/deep/.van-picker {
  width: 100vw;
}

span,
img {
  display: inline-block;
}

div,
span {
  -webkit-overflow-scrolling: touch;
}

.bj {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-size: 100% 100%;
}
</style>



