<template>
  <div class="bj">
    <div class="column">
      <span>demoBasic</span>
      vuex数据{{ dataTest }}

      <div>
        <img id="img" src="57410e4667f97362aa44af920924938c.jpeg" />
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import dayjs from "dayjs";

// import { dateTransfor } from "../utils";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  props: {
    // arr: {
    //   type: Array,
    //   default:()=>[]
    // },
  },
  components: {},
  data() {
    return {
      currentDate: null
    };
  },
  created() {},
  mounted() {
    const img = document.getElementById("img");
    setTimeout(function(){
/* recognize()启动识别的方法
 * "getCaptcha.jpg" 可以传入文件路径，文件对象，2d图像，二进制对象等
 * lang  识别的语言种类，下面再详细说
 * classify_bln_numeric_mode  识别过程中需要用到一些参数，类似一些常量的东东，也可以不填
 * progress() 执行识别过程中要进行的操作，简单的识别可以省略这个
 * packet 这个对象包含状态等内容，详细请console.info()
 * then()  相当于识别成功后的回调吧
 * data 自己console.info()
 */
 Tesseract.recognize(img, {
	lang: "eng",
	classify_bln_numeric_mode: 1
})
.progress(function(packet){
	//progressUpdate(packet)
	})
.then(function(data){
	alert(data.text);
	//progressUpdate({ status: 'done', data: data })
});
    },3000)
 

  },
  methods: {
    ...mapMutations({
      changeDataTest: "changeDataTest"
    })
  },
  watch: {
    //'list.xx':{} 监听一个属性的写法
    // msg: {
    //   handler(curVal, oldVal) {
    //     //改变其他变量的操作
    //   },
    //   immediately: true,
    //   deep: true,
    // },
  },
  computed: {
    // num() {
    //   //用其他变量计算 return出值
    //   return "num";
    // },

    //防止数据更新不及时 放computed获取
    ...mapState({
      dataTest: state => state.dataTest
    })
  },
  filters: {
    //这里定义的会覆盖全局
    //千分位
    // NumFormat: function (value) {
    //   if (value != null || "") {
    //     return value.toLocaleString("en-US");
    //   }
    // }
  }
};
</script>

<style lang="less" scoped>
// @import "../main.less";

input::-webkit-input-placeholder {
  // 设置输入框文字颜色
}

div::-webkit-scrollbar,
span::-webkit-scrollbar {
  // 设置滚动条样式
  display: none;
}

/deep/.van-picker {
  width: 100vw;
}

span,
img {
  display: inline-block;
}

div,
span {
  -webkit-overflow-scrolling: touch;
}

.bj {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  background-size: 100% 100%;
}
</style>



